
"use client";

import * as React from 'react';
import { categories as initialCategories } from "@/lib/data";
import { PageHeader } from "@/components/page-header";
import { CategoriesTable } from "@/components/dashboard/categories-table";
import type { Category } from "@/types";

export default function CategoriesPage() {
  const [categories, setCategories] = React.useState<Category[]>(initialCategories);

  const handleAddCategory = (newCategory: Omit<Category, 'id'>) => {
    setCategories((prevCategories) => [
      ...prevCategories,
      { ...newCategory, id: `cat-${Date.now()}` },
    ]);
  };
  
  return (
    <>
      <PageHeader
        title="Categories"
        description="Organize your items into categories."
      />
      <CategoriesTable categories={categories} onAddCategory={handleAddCategory} />
    </>
  );
}
